body {
  background-image: url(../public/assets/background.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
}

.container {
  width: 50%;
  box-shadow: 0 0.125rem 0.4375rem 0.0625rem #5961f9;
  border-radius: 0.625rem;
  background-color: #8bc6ec;
  background-image: linear-gradient(135deg, #8bc6ec 0%, #9599e2 100%);
}

.title {
  text-align: center;
}

.done {
  text-decoration: line-through;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.875rem;
  margin-top: 0.9375rem;
  box-shadow: 0 0.125rem 0.4375rem 0.0625rem #b8b7b5;
}

i {
  font-size: 2.5rem;
}

@media (max-width: 450px) {
  body {
    background-image: url(../public/assets/background-responsive.png);
    background-repeat: repeat;
    background-size: 100%;
  }

  .container{
    width: 90%;
  }

  .item {
    font-size: 18px;
  }

  i {
    font-size: 20px;
  }
}
